  align-items: center;
  list-style: none;
  margin-bottom: var(--rm-space-small);
  padding: var(--rm-space-medium) var(--rm-space-medium);
  border-radius: var(--rm-radius-x-large);
  text-decoration: none;
  color: var(--rm-color-primary-on-plus-3);
  transition: 300ms;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.sidebar-nav__item .material-icons {
  width: 20px;
  height: 20px;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: var(--rm-space-small);
}
@media (max-width: 512px) {
  .sidebar-nav__item {
    padding: var(--rm-space-medium);
  }
}
.sidebar-nav__item:last-of-type {
  margin-bottom: 0;
}
.sidebar-nav__item:hover {
  background: var(--rm-color-primary-hover);
  color: var(--rm-color-primary-on-hover);
}
.sidebar-nav__item.active {
  background: var(--rm-color-primary-base);
  color: var(--rm-color-primary-on-base);
}

.sidebar__footer {
  width: 100%;
}

.sidebar__logo {
  width: 25%;
  height: auto;
  display: flex;
  justify-content: center;
  margin: auto;
}

table.table {
  width: 100%;
  border-collapse: collapse;
}
table.table thead {
  background: var(--rm-color-background);
}
table.table th {
  padding: var(--rm-space-medium) var(--rm-space-medium);
  text-align: left;
  text-transform: uppercase;
  color: var(--rm-color-on-background);
  border-bottom: var(--rm-border-width) solid var(--rm-color-outline);
  font-size: var(--rm-font-small);
  font-weight: var(--rm-font-weight-normal);
}
table.table th:first-child {
  border-top-left-radius: var(--rm-radius-medium);
}
table.table th:last-child {
  border-top-right-radius: var(--rm-radius-medium);
}
table.table td {
  padding: var(--rm-font-x-small) var(--rm-space-medium);
  color: var(--rm-color-on-background);
  border-bottom: var(--rm-border-width) solid var(--rm-color-outline);
  font-size: var(--rm-font-small);
}

table.table--no-last-divider tr:last-child td {
  border-bottom: none;
}

.panel {
  --rm-panel-width: 40%;
  --rm-panel-transition-speed: 400ms;
  height: 100vh;
  width: var(--rm-panel-width);
  z-index: var(--rm-z-index-drawer);
  position: absolute;
  right: calc(-1 * var(--rm-panel-width));
  background: var(--rm-color-background);
  color: var(--rm-color-on-background);
  transition: right var(--rm-panel-transition-speed);
  overflow-y: scroll;
  padding-bottom: var(--rm-space-large);
}

.panel--active {
  right: 0;
  box-shadow: var(--rm-shadow-x-large);
}

.panel__close {
  float: right;
  cursor: pointer;
  display: flex;
  padding: var(--rm-space-medium);
  border-radius: 100%;
}
.panel__close:hover {
  background: var(--rm-color-neutral-variant-plus-3);
}

#panel-content {
  padding-top: var(--rm-space-large);
  padding-bottom: var(--rm-space-large);
}

.modal {
  --rm-modal-width: 564px;
  --rm-modal-transition-speed: 300ms;
  visibility: hidden;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: var(--rm-z-index-dialog);
  overflow: hidden;
  /*Prevent Chrome on Windows from adding a focus outline. For details, see*/
  /*https://github.com/twbs/bootstrap/pull/10951.*/
  outline: 0;
  /*We deliberately don't use `-webkit-overflow-scrolling: touch;` due to a
  gnarly iOS Safari bug: https://bugs.webkit.org/show_bug.cgi?id=158342
  See also https://github.com/twbs/bootstrap/issues/17695*/
}

.modal__backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: var(--rm-z-index-dialog-backdrop);
  background: black;
  visibility: hidden;
  opacity: 0;
  transition: all var(--rm-modal-transition-speed);
}

.modal__content {
  max-height: 500px;
  width: var(--rm-modal-width);
  padding: var(--rm-space-large);
  background: var(--rm-color-background);
  color: var(--rm-color-on-background);
  border-radius: var(--rm-radius-large);
  z-index: var(--rm-z-index-dialog-content);
  transform: scale(0.7);
  opacity: 0;
  overflow-y: auto;
  transition: all var(--rm-modal-transition-speed);
}

.modal--active {
  display: flex;
  visibility: visible;
}
.modal--active .modal__content {
  transform: scale(1);
  opacity: 1;
}
.modal--active .modal__backdrop {
  visibility: visible;
  opacity: 0.5;
}

.modal__body {
  overflow-y: auto;
}

.modal__close {
  float: right;
  cursor: pointer;
  padding: var(--rm-space-medium);
  border-radius: var(--rm-radius-circle);
}
.modal__close:hover {
  background: var(--rm-color-neutral-variant-plus-3);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--rm-space-small);
  background: var(--rm-color-primary-plus-2);
}

.nav {
  padding: 0.5rem 1rem;
  gap: 0.5rem;
  font-size: 2rem;
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  justify-content: flex-end;
}

.logo {
  float: left;
  margin-right: 10px;
  font-size: 1.7em;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: -1px;
  padding-top: 9px;
  font-weight: bold;
}

.logo:hover {
  color: #fff;
  text-decoration: none;
}

.nav-link {
  display: block;
  padding: 0.5rem;
}
.nav-link a {
  color: var(--rm-color-on-background);
}

/* footer */
footer {
  margin-top: 45px;
  padding-top: 5px;
  border-top: 1px solid #eaeaea;
  color: #777;
}
footer a {
  color: #555;
}
footer a:hover {
  color: #222;
}
footer small {
  float: left;
}
footer ul {
  float: right;
  list-style: none;
}
footer ul li {
  float: left;
  margin-left: 15px;
}

.debug_dump {
  clear: both;
  float: left;
  width: 100%;
  margin-top: 45px;
}

/* sidebar */
aside section.user_info {
  margin-top: 20px;
}
aside section {
  padding: 10px 0;
  margin-top: 20px;
}
aside section:first-child {
  border: 0;
  padding-top: 0;
}
aside section span {
  display: block;
  margin-bottom: 3px;
  line-height: 1;
}
aside section h1 {
  font-size: 1.4em;
  text-align: left;
  letter-spacing: -1px;
  margin-bottom: 3px;
  margin-top: 0px;
}

.gravatar {
  float: left;
  margin-right: 10px;
}

.gravatar_edit {
  margin-top: 15px;
}

/* forms */
input, textarea, select, .uneditable-input {
  border: 1px solid #bbb;
  width: 100%;
  margin-bottom: 15px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 1rem;
}

input {
  height: auto !important;
}

.error_explanation {
  color: red;
}
.error_explanation ul {
  color: red;
  margin: 0 0 30px 0;
}

/* Dropdown menu */
.dropdown-menu.active {
  display: block;
}

:root {
  --rm-color-primary-h: 234;
  --rm-color-primary-s: 32%;
  --rm-color-primary-l: 52%;
  --rm-color-secondary-h: 18;
  --rm-color-secondary-s: 7%;
  --rm-color-secondary-l: 72%;
  --rm-color-tertiary-h: 227;
  --rm-color-tertiary-s: 33%;
  --rm-color-tertiary-l: 80%;
  @import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
  --rm-font-family: "Poppins", sans-serif;
  --rm-card-padding: var(--rm-space-large);
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-content: center;
  background-color: var(--rm-color-primary-base);
  color: var(--rm-color-on-background);
  padding: var(--rm-space-large);
}

a.nav-bar__brand {
  text-transform: uppercase;
  font-size: var(--rm-font-2x-large);
  font-weight: var(--rm-font-weight-bold);
  text-decoration: none;
  padding: var(--rm-space-medium);
  color: var(--rm-color-on-background);
}

a.nav-bar__link {
  text-transform: uppercase;
  font-size: var(--rm-font-large);
  font-weight: var(--rm-font-weight-bold);
  text-decoration: none;
  padding: var(--rm-space-medium);
  color: var(--rm-color-on-background);
  border-bottom: 1rem solid var(--rm-color-primary-base);
}

a.nav-bar__link:hover {
  border-bottom: 1rem solid var(--rm-color-on-background);
}

a.nav-bar__link--active {
  border-bottom: 1rem solid var(--rm-color-on-background);
}