@mixin color-varieties {
  // Primary HSL
  --rm-color-primary-h: 234;
  --rm-color-primary-s: 32%;
  --rm-color-primary-l: 52%;

  // Secondary HSL
  --rm-color-secondary-h: 18;
  --rm-color-secondary-s: 7%;
  --rm-color-secondary-l: 72%;

  // Tertiary HSL
  --rm-color-tertiary-h: 227;
  --rm-color-tertiary-s: 33%;
  --rm-color-tertiary-l: 80%;
}

@mixin fonts {
  @import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
  --rm-font-family: 'Poppins', sans-serif;
}

:root {
  @include color-varieties;

  @include fonts;

  --rm-card-padding: var(--rm-space-large);
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-content: center;
  background-color: var(--rm-color-primary-base);
  color: var(--rm-color-on-background);
  padding: var(--rm-space-large)
}

a.nav-bar__brand {
  text-transform: uppercase;
  font-size: var(--rm-font-2x-large);
  font-weight: var(--rm-font-weight-bold);
  text-decoration: none;
  padding: var(--rm-space-medium);
  color: var(--rm-color-on-background);
}

a.nav-bar__link {
  text-transform: uppercase;
  font-size: var(--rm-font-large);
  font-weight: var(--rm-font-weight-bold);
  text-decoration: none;
  padding: var(--rm-space-medium);
  color: var(--rm-color-on-background);
  border-bottom: 1rem solid var(--rm-color-primary-base);
}

a.nav-bar__link:hover {
  border-bottom: 1rem solid var(--rm-color-on-background);
}

a.nav-bar__link--active {
  border-bottom: 1rem solid var(--rm-color-on-background); 
}